import React, { useState, useEffect } from 'react'
import { Navigate } from 'react-router-dom'
import { BarChart3, TrendingUp, Clock, CheckCircle, AlertTriangle, Leaf, Plus, Edit, Trash2 } from 'lucide-react'
import { useAuth } from '../contexts/AuthContext'
import api from '../services/api'

const Dashboard = () => {
  const { user, isAuthenticated } = useAuth()
  const [activeTab, setActiveTab] = useState('overview')
  const [stats, setStats] = useState({
    totalPredictions: 0,
    todayPredictions: 0,
    avgConfidence: 0,
    healthyPlants: 0,
    diseasedPlants: 0
  })
  const [recentPredictions, setRecentPredictions] = useState([])
  const [plants, setPlants] = useState([])
  const [loading, setLoading] = useState(true)
  const [showPlantForm, setShowPlantForm] = useState(false)
  const [editingPlant, setEditingPlant] = useState(null)

  useEffect(() => {
    if (isAuthenticated) {
      loadDashboardData()
      if (user?.role === 'admin') {
        loadPlants()
      }
    }
  }, [isAuthenticated, user])

  const loadDashboardData = async () => {
    try {
      setLoading(true)
      
      // Load prediction stats
      const statsResponse = await api.get('/predict/stats')
      if (statsResponse.data.success) {
        setStats(statsResponse.data.data)
      }

      // Load recent predictions
      const historyResponse = await api.get('/predict/history?limit=5')
      if (historyResponse.data.success) {
        setRecentPredictions(historyResponse.data.data.predictions)
      }

      // Load admin dashboard if user is admin
      if (user?.role === 'admin') {
        const dashboardResponse = await api.get('/admin/dashboard')
        if (dashboardResponse.data.success) {
          setStats(prev => ({
            ...prev,
            ...dashboardResponse.data.data.overview
          }))
        }
      }
    } catch (error) {
      console.error('Failed to load dashboard data:', error)
    } finally {
      setLoading(false)
    }
  }

  const loadPlants = async () => {
    try {
      const response = await api.get('/admin/plants')
      if (response.data.success) {
        setPlants(response.data.data.plants)
      }
    } catch (error) {
      console.error('Failed to load plants:', error)
    }
  }

  const handleCreatePlant = async (plantData) => {
    try {
      const response = await api.post('/admin/plants', plantData)
      if (response.data.success) {
        setPlants([response.data.data, ...plants])
        setShowPlantForm(false)
        alert('Plant created successfully!')
      }
    } catch (error) {
      console.error('Failed to create plant:', error)
      alert('Failed to create plant: ' + (error.response?.data?.error || error.message))
    }
  }

  const handleUpdatePlant = async (id, plantData) => {
    try {
      const response = await api.put(`/admin/plants/${id}`, plantData)
      if (response.data.success) {
        setPlants(plants.map(p => p.id === id ? response.data.data : p))
        setEditingPlant(null)
        alert('Plant updated successfully!')
      }
    } catch (error) {
      console.error('Failed to update plant:', error)
      alert('Failed to update plant: ' + (error.response?.data?.error || error.message))
    }
  }

  const handleDeletePlant = async (id) => {
    if (confirm('Are you sure you want to delete this plant?')) {
      try {
        const response = await api.delete(`/admin/plants/${id}`)
        if (response.data.success) {
          setPlants(plants.filter(p => p.id !== id))
          alert('Plant deleted successfully!')
        }
      } catch (error) {
        console.error('Failed to delete plant:', error)
        alert('Failed to delete plant: ' + (error.response?.data?.error || error.message))
      }
    }
  }

  if (!isAuthenticated) {
    return <Navigate to="/login" replace />
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Dashboard
        </h1>
        <p className="text-gray-600">
          Welcome back, {user?.name || user?.username}! Here's your plant disease detection overview.
        </p>
      </div>

      {/* Admin Tabs */}
      {user?.role === 'admin' && (
        <div className="mb-8">
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8">
              <button
                onClick={() => setActiveTab('overview')}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'overview'
                    ? 'border-primary-500 text-primary-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                Overview
              </button>
              <button
                onClick={() => setActiveTab('plants')}
                className={`py-2 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'plants'
                    ? 'border-primary-500 text-primary-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                Manage Plants
              </button>
            </nav>
          </div>
        </div>
      )}

      {/* Overview Tab */}
      {activeTab === 'overview' && (
        <>
          {/* Stats Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Total Predictions</p>
              <p className="text-3xl font-bold text-gray-900">{stats.totalPredictions}</p>
            </div>
            <div className="p-3 bg-primary-100 rounded-full">
              <BarChart3 className="h-6 w-6 text-primary-600" />
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Today's Predictions</p>
              <p className="text-3xl font-bold text-gray-900">{stats.todayPredictions}</p>
            </div>
            <div className="p-3 bg-secondary-100 rounded-full">
              <Clock className="h-6 w-6 text-secondary-600" />
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Avg Confidence</p>
              <p className="text-3xl font-bold text-gray-900">{stats.avgConfidence}%</p>
            </div>
            <div className="p-3 bg-green-100 rounded-full">
              <TrendingUp className="h-6 w-6 text-green-600" />
            </div>
          </div>
        </div>

        <div className="card">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600">Healthy vs Diseased</p>
              <p className="text-3xl font-bold text-gray-900">
                {stats.healthyPlants}:{stats.diseasedPlants}
              </p>
            </div>
            <div className="p-3 bg-blue-100 rounded-full">
              <Leaf className="h-6 w-6 text-blue-600" />
            </div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Recent Predictions */}
        <div className="lg:col-span-2">
          <div className="card">
            <h2 className="text-xl font-semibold text-gray-800 mb-6">
              Recent Predictions
            </h2>
            <div className="space-y-4">
              {recentPredictions.map((prediction) => (
                <div key={prediction.id} className="flex items-center space-x-4 p-4 bg-gray-50 rounded-lg">
                  <div className="w-16 h-16 bg-gray-200 rounded-lg flex items-center justify-center">
                    <Leaf className="h-8 w-8 text-gray-400" />
                  </div>
                  <div className="flex-1">
                    <h3 className="font-semibold text-gray-800">{prediction.prediction}</h3>
                    <div className="flex items-center space-x-4 text-sm text-gray-600">
                      <span>Confidence: {prediction.confidence}%</span>
                      <span>{prediction.timestamp}</span>
                    </div>
                  </div>
                  <div className={`flex items-center space-x-1 ${
                    prediction.status === 'healthy' ? 'text-green-600' : 'text-red-600'
                  }`}>
                    {prediction.status === 'healthy' ? (
                      <CheckCircle className="h-5 w-5" />
                    ) : (
                      <AlertTriangle className="h-5 w-5" />
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Health Overview */}
        <div className="space-y-6">
          <div className="card">
            <h2 className="text-xl font-semibold text-gray-800 mb-6">
              Plant Health Overview
            </h2>
            <div className="space-y-4">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <CheckCircle className="h-5 w-5 text-green-500" />
                  <span className="text-gray-700">Healthy Plants</span>
                </div>
                <span className="font-semibold text-gray-900">{stats.healthyPlants}</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div 
                  className="bg-green-500 h-2 rounded-full" 
                  style={{ 
                    width: `${(stats.healthyPlants / (stats.healthyPlants + stats.diseasedPlants)) * 100}%` 
                  }}
                ></div>
              </div>

              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-2">
                  <AlertTriangle className="h-5 w-5 text-red-500" />
                  <span className="text-gray-700">Diseased Plants</span>
                </div>
                <span className="font-semibold text-gray-900">{stats.diseasedPlants}</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div 
                  className="bg-red-500 h-2 rounded-full" 
                  style={{ 
                    width: `${(stats.diseasedPlants / (stats.healthyPlants + stats.diseasedPlants)) * 100}%` 
                  }}
                ></div>
              </div>
            </div>
          </div>

          <div className="card">
            <h2 className="text-xl font-semibold text-gray-800 mb-6">
              Quick Actions
            </h2>
            <div className="space-y-3">
              <button className="w-full btn-primary">
                New Classification
              </button>
              <button className="w-full btn-secondary">
                Export Reports
              </button>
              <button className="w-full bg-gray-200 hover:bg-gray-300 text-gray-700 font-semibold py-2 px-4 rounded-lg transition-colors">
                View All History
              </button>
            </div>
          </div>
        </div>
      </div>
        </>
      )}

      {/* Plants Management Tab */}
      {activeTab === 'plants' && user?.role === 'admin' && (
        <div className="space-y-6">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold text-gray-900">Plant Management</h2>
            <button
              onClick={() => setShowPlantForm(true)}
              className="btn-primary flex items-center space-x-2"
            >
              <Plus className="h-5 w-5" />
              <span>Add Plant</span>
            </button>
          </div>

          <div className="card">
            <div className="overflow-x-auto">
              <table className="min-w-full table-auto">
                <thead>
                  <tr className="border-b border-gray-200">
                    <th className="text-left py-3 px-4 font-semibold text-gray-700">Name</th>
                    <th className="text-left py-3 px-4 font-semibold text-gray-700">Scientific Name</th>
                    <th className="text-left py-3 px-4 font-semibold text-gray-700">Severity</th>
                    <th className="text-left py-3 px-4 font-semibold text-gray-700">Model Class ID</th>
                    <th className="text-left py-3 px-4 font-semibold text-gray-700">Status</th>
                    <th className="text-left py-3 px-4 font-semibold text-gray-700">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {plants.map((plant) => (
                    <tr key={plant.id} className="border-b border-gray-100">
                      <td className="py-3 px-4">{plant.name}</td>
                      <td className="py-3 px-4 text-gray-600">{plant.scientificName}</td>
                      <td className="py-3 px-4">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                          plant.severity === 'Low' ? 'bg-green-100 text-green-800' :
                          plant.severity === 'Medium' ? 'bg-yellow-100 text-yellow-800' :
                          plant.severity === 'High' ? 'bg-orange-100 text-orange-800' :
                          'bg-red-100 text-red-800'
                        }`}>
                          {plant.severity}
                        </span>
                      </td>
                      <td className="py-3 px-4">{plant.modelClassId}</td>
                      <td className="py-3 px-4">
                        <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                          plant.isActive ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'
                        }`}>
                          {plant.isActive ? 'Active' : 'Inactive'}
                        </span>
                      </td>
                      <td className="py-3 px-4">
                        <div className="flex space-x-2">
                          <button
                            onClick={() => setEditingPlant(plant)}
                            className="text-blue-600 hover:text-blue-900"
                          >
                            <Edit className="h-4 w-4" />
                          </button>
                          <button
                            onClick={() => handleDeletePlant(plant.id)}
                            className="text-red-600 hover:text-red-900"
                          >
                            <Trash2 className="h-4 w-4" />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      )}

      {/* Plant Form Modal */}
      {(showPlantForm || editingPlant) && (
        <PlantForm
          plant={editingPlant}
          onSubmit={editingPlant ? (data) => handleUpdatePlant(editingPlant.id, data) : handleCreatePlant}
          onCancel={() => {
            setShowPlantForm(false)
            setEditingPlant(null)
          }}
        />
      )}
    </div>
  )
}

// Plant Form Component
const PlantForm = ({ plant, onSubmit, onCancel }) => {
  const [formData, setFormData] = useState({
    name: plant?.name || '',
    scientificName: plant?.scientificName || '',
    description: plant?.description || '',
    symptoms: plant?.symptoms || '',
    treatment: plant?.treatment || '',
    prevention: plant?.prevention || '',
    severity: plant?.severity || 'Medium',
    modelClassId: plant?.modelClassId || '',
    imageUrl: plant?.imageUrl || '',
    isActive: plant?.isActive !== undefined ? plant.isActive : true
  })

  const handleSubmit = (e) => {
    e.preventDefault()
    onSubmit(formData)
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        <div className="p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">
            {plant ? 'Edit Plant' : 'Add New Plant'}
          </h3>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Plant Name *
                </label>
                <input
                  type="text"
                  required
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Scientific Name
                </label>
                <input
                  type="text"
                  value={formData.scientificName}
                  onChange={(e) => setFormData({...formData, scientificName: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
                />
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Severity *
                </label>
                <select
                  required
                  value={formData.severity}
                  onChange={(e) => setFormData({...formData, severity: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
                >
                  <option value="Low">Low</option>
                  <option value="Medium">Medium</option>
                  <option value="High">High</option>
                  <option value="Critical">Critical</option>
                </select>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Model Class ID * {!plant && '(unique)'}
                </label>
                <input
                  type="number"
                  required
                  disabled={!!plant}
                  value={formData.modelClassId}
                  onChange={(e) => setFormData({...formData, modelClassId: parseInt(e.target.value)})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 disabled:bg-gray-100"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Description
              </label>
              <textarea
                rows={3}
                value={formData.description}
                onChange={(e) => setFormData({...formData, description: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Symptoms
              </label>
              <textarea
                rows={3}
                value={formData.symptoms}
                onChange={(e) => setFormData({...formData, symptoms: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Treatment
              </label>
              <textarea
                rows={3}
                value={formData.treatment}
                onChange={(e) => setFormData({...formData, treatment: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Prevention
              </label>
              <textarea
                rows={3}
                value={formData.prevention}
                onChange={(e) => setFormData({...formData, prevention: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Image URL
              </label>
              <input
                type="url"
                value={formData.imageUrl}
                onChange={(e) => setFormData({...formData, imageUrl: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500"
              />
            </div>

            <div className="flex items-center">
              <input
                type="checkbox"
                id="isActive"
                checked={formData.isActive}
                onChange={(e) => setFormData({...formData, isActive: e.target.checked})}
                className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
              />
              <label htmlFor="isActive" className="ml-2 text-sm font-medium text-gray-700">
                Active
              </label>
            </div>

            <div className="flex justify-end space-x-4 pt-4">
              <button
                type="button"
                onClick={onCancel}
                className="px-4 py-2 text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
              >
                Cancel
              </button>
              <button
                type="submit"
                className="btn-primary"
              >
                {plant ? 'Update Plant' : 'Create Plant'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Dashboard 